@mixin border-bottom() {
  border-bottom: 2px solid $border-bottom-color; }

@mixin video-items() {
  margin: 68px 0 0 11px;
  width: 261px;
  height: 172px;
  background: $video-item-color; }

@mixin header() {
  .header {
    position: relative;
    height: 62px;
    @include border-bottom;
    .title {
      line-height: 62px;
      font-size: 28px;
      color: $content-title-color; }
    .pagination {
      position: absolute;
      right: 0;
      top: 12px;
      font-size: 0;
      .prev, .next {
        display: inline-block;
        width: 53px;
        height: 39px;
        line-height: 48px;
        text-align: center;
        box-sizing: border-box;
        border: 1px solid $pagination-btn-border-color;
        border-radius: 2px; }
      .next {
        margin-left: 14px; } } } }

@mixin header-btn() {
  position: absolute;
  right: 28px;
  top: 40px;
  width: 110px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  text-align: center;
  border-radius: 3px;
  background: #00b48f;
  & a {
    padding: 10px 38px; } }

@mixin video-list() {
  .content {
    display: flex;
    padding: 46px 0 120px 0;
    flex-wrap: wrap;
    @include border-bottom;
    .video-item {
      @include video-items;
      &:nth-of-type(-n + 4) {
        margin-top: 0; } } } }
